import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Router, ROUTES } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { DomSanitizer } from '@angular/platform-browser';

import { components, baseInputs } from '../data/components';

declare var ng: any;

@Component({
  selector: 'app-docs-components',
  templateUrl: './components.component.html',
  styleUrls: ['./components.component.scss'],
  preserveWhitespaces: true,
  encapsulation: ViewEncapsulation.Emulated,
})
export class DocsComponentsComponent implements OnInit {
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private sanitizer: DomSanitizer
  ) {}

  ngOnInit() {
    console.log('Inside docs comopnents component');
  }

  components = components;
  baseInputs = baseInputs;

  imageSources = {
    scullyImageLowRes: this.sanitizer.bypassSecurityTrustUrl(
      'data:image/png;base64,'
    ),

    scullyImagePrimitives: this.sanitizer.bypassSecurityTrustUrl(
      'data:image/svg+xml;base64,'
    ),

    scullyImageTraced: this.sanitizer.bypassSecurityTrustUrl(
      'data:image/svg+xml;base64,'
    ),
  };
}
